.footercontainer{
    background-color: #e2d8ed;
    padding: 20px;
}

.footertext{
    text-align: center;
    display: flex;
    justify-content: center;
    gap: 15px;
}

.footertext1{
    border-right: 1px solid rgb(98, 98, 98);
    padding-right: 15px;
}

.footerimages{
    display: flex;
    justify-content: center;
    align-items: center;
}

.footerfacebooklogo{
    height: 2em;
}

.footertwitterlogo{
    height: 1.85em;
    padding-left: 10px;
}

.footerdivider{
    width: 97%;
}

.footer-navigation{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
    /* padding-bottom: 20px; */
}

@media (max-width: 600px) {
    .footer-navigation{
        flex-direction: column;
        text-align: center;
    }
    .footertext{
        flex-direction: column;
    }
    
    .footertext1{
        border-right: 0;
        padding-right: 0;
    }
    
}