.certificate-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    gap: 20px;
}

.certificate-text{
    padding-right: 10px;
}

.certificate-image{
    height: 250px;
}

.certificate-accordion{
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

@media (max-width: 700px) {
    .certificate-container{
        flex-direction: column;
        gap: 20px;
    }
    .certificate-image{
        /* height: 250px; */
        width: 95%;
        height: 100%;
        max-height: 250px;
    }
}