.appbar-menuicon-container{
    background-color: #29184d;
    display: flex;
    justify-content: flex-end;
}

.Appbar{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-top: 20px;
    /* padding-bottom: 20px; */
}



/* .appbarlogocontainer{
    display: flex;
    align-items: center;
} */

.appbar-logo1{
    /* width: 18%; */
    /* padding-left: 50px; */
    height: 70px;
    padding-right: 30px;
    background-color: white;
}

.appbar-logo2{
    /* width: 18%; */
    padding-left: 30px;
    height: 100px;
}

.appbar-navigation{
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
    
}

.nav-link{
    text-decoration: none;
    /* padding-left: 15px;
    padding-right: 15px; */
    font-size: 110%;
    cursor: pointer;
    color: #551a8b;
}

/* .nav-link:last-child{
    padding-right: 30px;
} */

.nav-link:hover{
    color: blue;
}

@media (max-width: 600px) {
    .Appbar{
        flex-direction: column;
        gap: 30px;
        padding-bottom: 0;
    }
    .appbar-logo1{
        width: 95%;
        height: 100%;
        padding-right: 0;
        max-height: 70px;
        max-width: 250px;
    }
    
    .appbar-logo2{
        /* width: 18%; */
        width: 30%;
        padding-left: 0;
        height: 100%;
        max-height: 100px;
        max-width: 100px;
    }
}