.stripcontainer{
    background-color: #29184d;
}

.detailstrip{
    display: flex;
    background-color: #29184d;
    padding-top: 30px;
    padding-bottom: 30px;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
}

.strip{
    display: flex;
    /* align-items: center; */
    padding-left: 20px;
    padding-right: 20px;
    border-right: 3px solid white;
    flex: 25%;
}

.strip:first-child{
    flex: 35%;
}

.strip:last-child{
    /* flex: 20%; */
    border-right: 0;
}

.stripimage{
    /* width: 20%; */
    height: 5em;
    padding-left: 10px;
    padding-right: 10px;
}

.stripimage2{
    height: 4.5em;
    padding-left: 35px;
    padding-right: 35px;
    padding-top: 10px;
}

.textcontainer{
    color: white;
    padding-left: 10px;
}

.textheading{
    font-weight: bold;
    font-size: 150%;
}

@media (max-width: 900px) {
    .detailstrip{
        flex-direction: column;
        gap: 30px;
        /* gap: 80px; */
    }
    .strip{
        border-right: 0;
        flex: 100%;
        border-bottom: 3px solid white;
        padding-bottom: 20px;
    }
    .strip:last-child{
        border-bottom: 0;
    }
}

@media (max-width: 300px) {
    .strip{
        padding-left: 0;
        padding-right: 0;
    }
}