.App {
    background-color: white;
}

.section-header{
    font-weight: bold;
    text-align: center;
    font-size: 180%;
    background-color: #29184d;
    color: white;
    padding-top: 5px;
    padding-bottom: 5px;
}

.accordion-title{
    font-weight: bold;
}

.page-title{
    background-color: #29184d;
    height: 50px;
}


/* ------------Introduction--------------- */
.introduction{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 75%;
    margin-left: auto;
    margin-right: auto;    
}

@media (max-width: 1000px) {
    .introduction{
        width: 90%;
    }
}

.introductiontext{
    font-weight: bold;
    font-size: 250%;
    /* width: 30%; */
}

.introductionimage{
    height: 300px;
}

@media (max-width: 800px) {
    .introduction{
        width: 100%;
        flex-direction: column;
        gap: 20px;
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .introductiontext{
        text-align: center;
    }
    .introductionimage{
        width: 80%;
        height: 100%;
        margin-left: auto;
        margin-right: auto;
        max-width: 533px;
    }
}

.introduction-image-description{
    color: grey;
    text-align: center;
}

.btn-enroll-container{
    /* padding-left: 17em; */
    width: 66%;
    margin-left: auto;
    margin-right: auto;
}

.btn-enroll{
    padding: 12px;
    background-color: #29184d;
    color: white;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 4px;
    font-size: 95%;
}

.btn-enroll:hover{
    background-color: #421f8d;
    cursor: pointer;
}

@media (max-width: 800px) {
    .btn-enroll-container{
        display: flex;
        justify-content: center;
    }
}

/* ------------Course-description---------------- */
.course-decription{
    width: 75%;
    margin-left: auto;
    margin-right: auto;
}

.course-decription-title{
    font-weight: bold;
    font-size: 150%;
    padding-top: 1.5em;
    padding-bottom: 0.5em;
}

.course-description-text{
    padding-left: 0.5em;
}

.course-objectives-start{
    padding-left: 1em;
    padding-top: 0.5em;
}

/* ------------What-you-will-learn----------------- */
.what-you-will-learn{
    width: 75%;
    margin-left: auto;
    margin-right: auto;
}


/* ------------Course-Instructors------------------ */
.course-instructors-container{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
}

@media (max-width: 800px) {
    .course-instructors-container{
        flex-direction: column;
        gap: 80px;
    }
}

.course-instructor{
    display: flex;
    align-items: center;
    /* padding-bottom: 80px; */
    flex-direction: column;
    flex: 50%;
    padding: 20px;
    position:relative;
}

.instructors-image{
    /* width: %; */
    border-radius: 50%;
    /* width: 180px; */
    height: 200px;
    padding-bottom: 20px;
}

@media (max-width: 400px) {
    .instructors-image{
        height: 100%;
        width: 90%;
        max-height: 200px;

    }
}

.instructor-text{
    /* padding-left: 50px; */
    text-align: center;
}

.instructor-name{
    font-size: 160%;
    font-weight: bold;
}

.instructor-profile-link{
    position: absolute;
    left: 50%;
    bottom: 0px;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%)
}


