@media (max-width: 600px) {
    .home-introduction-text-container{
        padding-left: 2em;
    }
}

@media (max-width: 300px) {
    .home-introduction-text-title{
        font-size: 150%;
        font-weight: bold;
    }
    .home-introduction-text-main{
        font-size: 120%;
    }
}

.self-paced-certificate-course-text{
    text-align: center;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

.card-container{
    display: flex;
    justify-content: center;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    gap: 1em;
}

.card{
    padding: 5px;
    border: 1px solid black;
    border-radius: 5px;
    width: 25em;
    position: relative;
}

.card:hover{
    box-shadow: 0 0 15px #888888;
    transition: box-shadow 0.3s ease-in-out;
}

.card-image{
    width: 100%;
}

.card-title{
    text-decoration: underline;
    font-weight: bold;
    padding: 10px;

}

.card-text{
    padding: 10px;
    padding-bottom: 50px;
}

.card-footer{
    color: blue;
    padding: 10px;
    position: absolute;
    bottom: 0;
    left: 0;
}

@media (max-width: 900px) {
    .card-container{
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 500px) {
    .card{
        width: 100%;
    }
}


.program-experience-text{
    color: #29184d;
    text-align: center;
    font-weight: bold;
    font-size: 180%;
}

.program-experience-text2{
    color: #29184d;
    text-align: center;
    font-weight: bold;
    font-size: 180%;
    width: 95%;
    margin-left: auto;
    margin-right: auto;
}

.program-experience-text2-italics{
    display: inline-block;
    font-style: italic;
}

.program-experience-icons{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 100px;
    padding-top: 20px;
    padding-bottom: 10px;
    flex-wrap: wrap;
}



.program-experience-icons-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.program-experience-icondiv{
    display: flex;
    justify-content: center;
}

@media (max-width: 600px) {
    .program-experience-icons{
        flex-direction: column;
        gap: 50px;
    }
}